import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Match from './components/match'

const App = () => {

    const [name, setName] = useState("");
    const [nameSearch, setNameSearch] = useState("");
    const [matchInfo, setMatchInfo] = useState([]);
    const [notification, setNotification] = useState({ type: '', message: 'Enter IGN and hit search' });
    
    // When typing name into search box, auto updates the state of nameSearch
    const handleNameSearch = (e) => {
        setNameSearch(e.target.value);
    }

    // onsubmit, changes state of name
    const handleSubmit = (e) => {
        e.preventDefault();
        // updating state "takes time"
        setName(nameSearch);
        setMatchInfo([]);
    }

    // handle update - send request to update database
    const handleUpdate = () => {
        // Clicking update without typing in username does nothing
        if (name === "") {
            setNotification({ type: 'error', message: 'Please search for user before updating'})
        } else {
            setNotification({ type: 'processing', message: 'Updating. This may take up to a minute' })
            
            // console.log("Sending request to '/update' with { name : name }");

            axios.post(`/update`, {name: name})
            // axios.post(`http://localhost:3001/update`, {name: name})
                .then(response => {
                    setNotification({ type: 'success', message: 'Successfully updated match history.\n' +
                    'To load more matches, you may have to wait 2 minutes' })
                    setMatchInfo(response.data);
                })
                .catch(err => {
                    setNotification( {type: 'error', message: 'Error while updating. Please refresh the page or try again in 2 minutes.'})
                    console.log(err);

                })
        }
    }

   console.log(nameSearch);
    // When state of name changes, it takes time.
    // Once changed, this hook will send a get request to the backend
    useEffect(() => {
        // Need to check for blank string - if not, will send a post request right away
        if (name !== "") {
            setNotification({ type: 'processing', message: 'Searching...' })
            axios.post(`/search`, {name: name})
            // axios.post(`http://localhost:3001/search`, {name: name})
            .then(async (response) => {
                
                if (response.data.length === 0) {
                    setNotification({ type: 'error', message: 'Please update' })
                } else {
                    console.log(response.data);
                    setMatchInfo(response.data);
                    setNotification({ type: 'success', message: 'Success!' })
                }
            })
            .catch(err => {
                setNotification({ type: 'error', message: 'Please ensure that appropriate username is entered'})
                console.log(err);
            })
        }
    }, [name])

    const matchToShow = () => {
        return matchInfo.map(match => <Match key={match.matchId} match={match} userName={name} />) 
    }

    return (
        <div>
            <form onSubmit={handleSubmit}>
                <input type="text" value={nameSearch} placeholder="Enter user name" onChange={handleNameSearch}></input>
                <button className='search-button' type="submit" >Search</button>
                <button className ='update button' onClick={handleUpdate}>Update</button>
            </form>

            <div className="matchlist">
                
            <div className={`message ${notification.type}`}>
                {notification.message}
            </div>
            <div className='matches'>{matchToShow()}</div>
                
            </div>
        </div>
    )
}

export default App;