import React from 'react';

const timeToDate = (num) => {
    if (!num) {
        return ""
    }
    let date = new Date(num);
    let y = date.getFullYear();
    let m = date.getMonth();
    let d = date.getDate();
    let h = date.getHours();
    let i = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
    let day = date.getDay();
    const weekday = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"]
    return `${y}-${m+1}-${d} ${h}:${i} ${weekday[day]}`
}

const secToMinSec = (num) => {
    if (!num) return "";
    return `${Math.floor(num / 60)}:${num % 60}` ;
}

const Match = ({ match, userName }) => {
    const patch = "10.19.1"
    // If match is not an empty object, render; otherwise, don't
    if (match.description) {
        // console.log("match inside Match component", match);
        userName = userName.split(" ").join("").toLowerCase();
        let win, team;
        let role = match.role === "NONE" ? "" : match.role
        let playerOfInterest = match.playersInformation.find(player => {
            return player.userName.split(" ").join("").toLowerCase() === userName;    
        });
        // console.log("playerofinterest: ", playerOfInterest);
        
        if (playerOfInterest.id > 5) {
            team = "purple"
        } else if (playerOfInterest.id < 6) {
            team = "blue"
        };

        if (team === "blue" && match.blueTeamWin ) {
            win = true
        } else if (team === "purple" && !match.blueTeamWin) {
            win = true
        } else if (team === "blue" && !match.blueTeamWin) {
            win = false
        } else if (team === "purple" && match.blueTeamWin) {
            win = false
        }
        
        let outcome = win ? "Victory" : "Defeat"
        let kda = `${playerOfInterest.kills} / ${playerOfInterest.deaths} / ${playerOfInterest.assists}`
        let wards = playerOfInterest.wards.map(count => count === null ? 0 : count);

        const showItem0 = playerOfInterest.items[0] === 0 ? "" : <img className="item5" alt="" width="30" height="30" src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/item/${playerOfInterest.items[0]}.png`} />
        const showItem1 = playerOfInterest.items[1] === 0 ? "" : <img className="item5" alt="" width="30" height="30" src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/item/${playerOfInterest.items[1]}.png`} />
        const showItem2 = playerOfInterest.items[2] === 0 ? "" : <img className="item5" alt="" width="30" height="30" src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/item/${playerOfInterest.items[2]}.png`} />
        const showItem3 = playerOfInterest.items[3] === 0 ? "" : <img className="item5" alt="" width="30" height="30" src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/item/${playerOfInterest.items[3]}.png`} />
        const showItem4 = playerOfInterest.items[4] === 0 ? "" : <img className="item5" alt="" width="30" height="30" src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/item/${playerOfInterest.items[4]}.png`} />
        const showItem5 = playerOfInterest.items[5] === 0 ? "" : <img className="item5" alt="" width="30" height="30" src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/item/${playerOfInterest.items[5]}.png`} />
        const showItem6 = playerOfInterest.items[6] === 0 ? "" : <img className="item6" alt="" width="30" height="30" src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/item/${playerOfInterest.items[6]}.png`} />

        const summonerSpell1 = <img className="summ1" alt="" width="30" height="30" src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/spell/${playerOfInterest.summonerSpells[0]}.png`} />
        const summonerSpell2 = <img className="summ2" alt="" width="30" height="30" src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/spell/${playerOfInterest.summonerSpells[1]}.png`} />
        
        // issue with playerOfInterest.runes[0] = null (hail of blades)
        // quickfix
        let keystone = null
        if (playerOfInterest.runes[0] !== null)
            keystone = <img className="summ2" alt="" width="30" height="30" src={`https://raw.communitydragon.org/latest/plugins/rcp-be-lol-game-data/global/default/v1/${playerOfInterest.runes[0].icon.toLowerCase()}`} />
        const secondary = <img className="summ2" alt="" width="30" height="30" src={`https://raw.communitydragon.org/latest/plugins/rcp-be-lol-game-data/global/default/v1/${playerOfInterest.runes[1].icon.toLowerCase()}`} />
        return (
            <div className={`match-container ${outcome}`}>
                <div className="game-info">
                    <div className="description">({match.patch}) {match.description.replace(" games", "")}</div>
                    <div className="game-creation">{timeToDate(match.gameCreation)}</div>
                    <div className={`outcome`}>{outcome}</div>
                    <div className="game-duration">{secToMinSec(match.gameDuration)}</div>
                    
                </div>
                <div className="player-info">
                    <img width="30" height="30" alt=""
                            src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/champion/${playerOfInterest.champion}.png`} />
                    <div className="champion-played">{match.champion}</div>
                    <div className="role">{role}</div>
                    <div className="kda">{kda}</div>
                    <div className="wards">Wards BPK</div>
                    <div className="wardsKBP">{`${wards[0]}/${wards[1]}/${wards[2]}`}</div>
                </div>
                
                
                <div className="player-item-container">
                    <div className="item0-container">
                        {showItem0}
                    </div>
                    <div className="item1-container">
                        {showItem1}
                    </div>
                    <div className="item2-container">
                        {showItem2}
                    </div>
                    <div className="item3-container">
                        {showItem3}
                    </div>
                    <div className="item4-container">
                        {showItem4}
                    </div>
                    <div className="item5-container">
                        {showItem5}
                    </div>
                    <div className="item6-container">
                        {showItem6}
                    </div>
                    <div className="summ1-container">
                        {summonerSpell1}
                    </div>
                    <div className="summ2-container">
                        {summonerSpell2}
                    </div>
                    <div className="keystone-container">
                        {keystone}
                    </div>
                    <div className="secondary-container">
                        {secondary}
                    </div>

                </div>
                <div className="players-container">

                    <div className="blue-team">
                        <div className="blue-team-player"><img width="30" height="30" alt=""
                            src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/champion/${match.playersInformation[0].champion}.png`} />
                            {match.playersInformation[0].userName}
                        </div>
                        <div className="blue-team-player"><img width="30" height="30" alt=""
                            src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/champion/${match.playersInformation[1].champion}.png`} />
                            
                            {match.playersInformation[1].userName}</div>
                        <div className="blue-team-player"><img width="30" height="30" alt=""
                            src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/champion/${match.playersInformation[2].champion}.png`} />
                            {match.playersInformation[2].userName}</div>
                        <div className="blue-team-player"><img width="30" height="30" alt=""
                            src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/champion/${match.playersInformation[3].champion}.png`} />
                            {match.playersInformation[3].userName}</div>
                        <div className="blue-team-player"><img width="30" height="30" alt=""
                            src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/champion/${match.playersInformation[4].champion}.png`} />
                            {match.playersInformation[4].userName}</div>
                    </div>
                    
                    <div className="red-team">
                        <div className="red-team-player"><img width="30" height="30" alt=""
                            src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/champion/${match.playersInformation[5].champion}.png`} />
                            {match.playersInformation[5].userName}</div>
                        <div className="red-team-player"><img width="30" height="30" alt=""
                            src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/champion/${match.playersInformation[6].champion}.png`} />
                            {match.playersInformation[6].userName}</div>
                        <div className="red-team-player"><img width="30" height="30" alt=""
                            src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/champion/${match.playersInformation[7].champion}.png`} />
                            {match.playersInformation[7].userName}</div>
                        <div className="red-team-player"><img width="30" height="30" alt=""
                            src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/champion/${match.playersInformation[8].champion}.png`} />
                            {match.playersInformation[8].userName}</div>
                        <div className="red-team-player"><img width="30" height="30" alt=""
                            src={`http://ddragon.leagueoflegends.com/cdn/${patch}/img/champion/${match.playersInformation[9].champion}.png`} />
                            {match.playersInformation[9].userName}</div>                        
                    </div>

                </div>
            </div>
        )
    }

    return (
        <div className="match-container">
            Waiting ... 
        </div>
    )
}

export default Match;